import NavBar from '@/components/NavBar'
import store from '../../store'

import bfrl from '../../assets/img/nav_bar/home_icon_bfrl.png' // 
import bbsq from '../../assets/img/nav_bar/kq_icon_bbsq .png' // 
import xzzd from '../../assets/img/nav_bar/zd_icon_xzzd.png' // 
import tjhd from '../../assets/img/nav_bar/hd_icon_tjhd.png' // 
import setu from '../../assets/img/nav_bar/set_up.png' // 
import bluePanIcon from '../../assets/img/nav_bar/icon_revise@2x.png' // 
// import wdbf from '../assets/img/personal/me_icon_wdbf@2x.png' // 
import tjrw from '../../assets/img/personal/home_icon_tjrw@2x.png'
import sjtb from '../../assets/img/nav_bar/home_icon_sjtb@2x.png'

const show = () => {
  store.dispatch('navBar/showNavBar')
}
const hide = () => {
  store.dispatch('navBar/hideNavBar')
}
const setRightBtn = (title = '', imageUrl = '', linkTo = '') => {
  store.dispatch('navBar/setNavBarRightBtn', { title, imageUrl, linkTo })
}
const setLeftBtn = (title = '', imageUrl = '', linkTo = '') => {
  store.dispatch('navBar/setNavBarLeftBtn', { title, imageUrl, linkTo })
}
const setMidTitle = (midTitle) => {
  store.dispatch('navBar/setNavBarMiddleTitle', midTitle)
}

//////////

// store.dispatch('navBar/setNavBarRightBtn', { title: '', imageUrl: '', linkTo: '' })

