//////////

const plugin = {
  install(Vue) {
    Vue.component('NavBar', NavBar)
    const $vm = Vue
    if (!$vm.$navbar) {
      $vm.prototype.$navbar = {
        show,
        hide,
        setRightBtn,
        setLeftBtn,
        setMidTitle,

        // home
        homeStyle,
        syncDataStyle,
        // 
        normalVisitStyle,
        tempPhotoCollectionStyle,
        selectPhotoTypeStyle,
        // 
        abnormalVisitStyle,
        // 
        attendanceStyle,
        attendanceCheckInStyle,
        attendanceCheckOutStyle,
        // 
        storeStyle,
        storeDetailStyle,
        addStoreStyle,
        // 
        campaignStyle,
        createCampaignStyle,
        campaignDetailStyle,
        campaignSelectStoreStyle,
        // 
        personalStyle,
        myApplicationRecordStyle,
        myApplicationDetailStyle,
        myApplicationRequestStyle,
        myAttendanceStyle,
        myAttendanceInfoStyle,
        // 
        mySettingStyle,
        versionInfoStyle,
        // 
        myVisitStyle,
        checkVisitStyle,
        personalSelectStoreStyle,
        // 
        developingStyle,
      }
    }
  },
  // navbarRightBtnListener,
}

export default plugin



