import { mapGetters, mapActions, mapMutations } from 'vuex'
import CommonHeader from '@/components/commonHeader'
import PhotoCollector from '@/components/PhotoCollector'
// import AMap from 'vue-amap'// 
// import AMap from 'AMap'// 

export default {
  components: {
    CommonHeader,
    PhotoCollector,
  },
  data() {
    return {
      detailType: '', // (addsave)
      eventTypeList: [], // 
      readOnly: false, // 
      selectStore: {}, // ""
      eventContentValue: '',
      photoArray: [],
      // eventDetail: {
      //   eventSid: '', // sid()
      //   type: '', // EventType(01)
      //   storeId: '',
      //   storeName: '',
      //   target_type: '', // (01)
      //   address: '', // 
      //   description: '', // 
      //   photoList: [{
      //     photoSid: '',
      //     photoUrl: '',
      //   }], // 
      // },
      eventEle: {},
    }
  },
  beforeRouteUpdate(to, from, next) {
    if (from.fullPath.indexOf('selectStore') > -1) { // 
      this.$nextTick(() => {
        this.selectStore = this.$route.query.selectStore
        this.eventDetail.storeId = this.$route.query.selectStore.storeId
        this.eventDetail.storeName = this.$route.query.selectStore.storeName
        this.eventDetail.address = this.$route.query.selectStore.address
      })
    }
    next()
  },
  mounted() {
    this.$navbar.campaignDetailStyle()
    this.eventEle = this.$route.params
    const pickDate = this.$filter.strToDateEnd(this.eventEle.eventDate)
    if (this.$filter.isAfterToday(pickDate)) {
      this.readOnly = false
    } else {
      this.readOnly = true
    }
    this.getEventTypeList()
    this.photoArray = [...this.getEventDetail.photoInfoList]
    // if (this.getCreateCampaignTempEle.description !== undefined) {
    //   this.eventContentValue = this.getCreateCampaignTempEle.description
    // }
  },
  computed: {
    ...mapGetters('campaign', [
      'getEventList',
      'getEventDetail',
    ]),
    eventDetail() {
      return this.getEventDetail
    },
    eventContent: {
      set(newValue) {
        this.SET_EVENT_DETAIL_DESCRIPTION(newValue)
        // this.eventContentValue = newValue
      },
      get() {
        return this.getEventDetail.description
      },
    },
    showStoreName() {
      if ( this.eventEle.storeName === ('' || undefined)) {
        return false
      } else {
        return true
      }
    },
  },
  methods: {
    ...mapActions('normalVisit', [
      'takePicture', // 2 param: photoType = '', specialDisplayTypeSid = ''
      'checkPhotoArrayCount',
    ]),
    ...mapActions('personal', [
      'queryAttributeInfoList',
    ]),
    ...mapActions('campaign', [
      'submitEvent',
    ]),
    ...mapMutations('campaign', [
      'SET_EVENT_DETAIL_DESCRIPTION',
    ]),
    takePhoto(photoType) { // 
      if (this.readOnly) {
        return
      }
      this.checkPhotoArrayCount({self:this, photoArray:this.photoArray}).then(() => {
        this.takePicture({ photoType }).then((resp) => {
          this.photoArray.push(resp)
        })
      })
    },
    editable() { // 
      // const month = (new Date().getMonth() + 1 > 9) ? new Date().getMonth() + 1 : `0${new Date().getMonth() + 1}`
      // const today = [new Date().getFullYear(), month, new Date().getDate()]
      // const eventDate = this.$route.query.eventDate.split('-')
      const today = this.$filter.dateToStrYYYYmmDD(new Date)
      const editable = today === this.$route.query.eventDate
      if (editable) {
        this.readOnly = false
      } else {
        this.readOnly = true
      }
    },
    getEventTypeList() { // 
      this.queryAttributeInfoList('EVENT').then((resp) => {
        this.eventTypeList = resp.body
      })
    },
    radioCheck(eventTypeName) { // 
      // if (eventTypeName === '' && this.detailType === 'add') {
      //   this.$router.push('/campaign/detail/selectStore')
      // } else if (eventTypeName !== '' && this.detailType === 'add') {
      //   this.eventDetail.address = ''
      //   this.loadmap()
      // }
    },
    loadmap() { // Amap
      const mapObj = new AMap.Map('amapBlock')
      mapObj.plugin(['AMap.Geolocation'], () => {
        const geolocation = new AMap.Geolocation({
          enableHighAccuracy: true, //  :true
          timeout: 10000, //  10
          maximumAge: 0, // 00
          convert: true, // true
        })
        geolocation.getCurrentPosition()
        // 
        AMap.event.addListener(geolocation, 'complete', (result) => {
          this.regeocoder(result.position.lng, result.position.lat)
        })
        // 
        AMap.event.addListener(geolocation, 'error', (result) => {
          console.log(result)
        })
      })
    },
    regeocoder(lng, lat) { // 
      // 
      // const geocoder = new AMap.Geocoder({
      //   radius: 1000,
      //   extensions: 'all',
      // })
      // geocoder.getAddress([lng, lat], (status, result) => {
      //   console.log(`getAddress status: ${status}`)
      //   console.log(`getAddress result: ${result}`)
      //   if (status === 'complete' && result.info === 'OK') {
      //     this.eventDetail.address = result.regeocode.formattedAddress
      //     // this.geocoder_CallBack(result)
      //   }
      // })

      // 
      let geocoder
      AMap.service('AMap.Geocoder', () => {
        geocoder = new AMap.Geocoder({})
      })
      const lanlat = new AMap.LngLat(lng, lat)
      const lnglatXY = [lanlat.getLng(), lanlat.getLat()]
      console.log(lnglatXY)
      geocoder.getAddress(lnglatXY, (status, result) => {
        console.log(`getAddress status: ${status}`)
        console.log(`getAddress result: ${result}`)
        if (status === 'complete' && result.info === 'OK') {
          this.eventDetail.address = result.regeocode.formattedAddress
          // this.geocoder_CallBack(result)
        }
      })
    },
    // geocoder_CallBack(data) { // 
    //   console.log('geocoder_CallBack data')
    //   console.log(data)
    //   const address = data.regeocode.formattedAddress
    //   this.eventDetail.address = address
    // },
    btnCancel() { // 
      this.$router.push('/campaign')
    },
    btnSubmit() { // 
      const photoList = this.photoArray.map((photoEle) => photoEle.photoSid)
      const { eventSid, type, storeId, storeName, target_type, address } = this.getEventDetail
      const param = {
        submitType: 1, // : 0 , 1
        type: this.eventType, // : 0 , 1
        target_type: this.eventDetail.target_type === 'STORE' ? '0' : '1', // : 0 , 1
        eventSid,
        storeId,
        storeName,
        address,
        description: this.getEventDetail.description,
        photoList,
      }
      const self = this
      this.submitEvent(param).then(() => {
        this.$vux.alert.show({
          title: '',
          content: ``,
          onHide(){
            self.$router.push('campaign')
          }
        })
      })
    },
    checkInputValue(value) { // &"'<>
      let returnValue = ''
      if (value) {
        returnValue = value.trim()
        .replace(/&/g, '&#38;')
        .replace(/"/g, '&#34;')
        .replace(/'/g, '&#39;')
        .replace(/</g, '&#60;')
        .replace(/>/g, '&#62;')
      }
      return returnValue
    },
  },
}



