    ///
    filteredList() {
      const tempList = this.getStoreList.filter((storeInfo) => {
        const isIncludingStoreName = storeInfo.storeName.includes(this.searchText)
        const isIncludingStoreOwner = storeInfo.storeOwner.includes(this.searchText)
        const isIncludingMobile = storeInfo.mobile.includes(this.searchText)
        const isIncludingPhone = storeInfo.phone.includes(this.searchText)
        const isIncludingAddress = storeInfo.address.includes(this.searchText)
        const isIncludingState = storeInfo.storeState.includes(this.searchText)
        return isIncludingStoreName || isIncludingStoreOwner || isIncludingMobile || isIncludingPhone || isIncludingAddress || isIncludingState
      })
      return tempList
    },
