import CoverDialog from '@/pages/home/components/CoverDialog'
import { mapGetters, mapActions, mapMutations } from 'vuex'

const CHECK_VISIT_PATH = '/check_visit'
const NORMAL_VISIT_PATH = '/normal_visit'
const ABNORMAL_VISIT_PATH = '/abnormal_visit'
export default {
  components: {
    CoverDialog,
  },
  data() {
    return {
      NORMAL: 'normal',
      ABNORMAL: 'abnormal',
      coverDialogVisible: false,
      alertVisible: false,
      alertContent: '',
      dailyVisitPlanArray: [],
      selectedStore: {},
    }
  },
  computed: {
    ...mapGetters('home', [
      'getDailyVisitPlan',
      'getSelectedVisitEle',
    ]),
    unfinishedVisits() {
      let unfinishedVisit = []
      this.getDailyVisitPlan.forEach((visit) => {
        if (visit.visitStatus == 1) {
          unfinishedVisit.push(visit)
        }
      })
      return unfinishedVisit
    }
  },
  beforeRouteLeave(to, from, next) {
    this.$parent.$off('navbarRightBtnClick') // 
    next()
  },
  mounted() {
    this.$navbar.show()
    this.$navbar.homeStyle()
    this.$navbar.myVisitPreviousPage = 'home'
    this.$navbar.checkInfoPreviousPage = 'home'
    this.$navbar.normalVisitPreviousPage = 'home'
    this.$navbar.abnormalVisitPreviousPage = 'home'
    this.$navbar.syncDataPreviousPage = '/home'
    // this.$store.commit(`navBar/SET_NAV_BAR_RIGHT_BUTTON_EVENT`,{ rightBtnEvent:this.rightBtnEvent })

    this.$parent.$on('navbarRightBtnClick',(param => { // 
      this.$router.push('sync_data')
    }))

    this.queryDailyVisitPlan()
    this.queryEmpInfo().then((resp) => {})
  },
  created() {
    // this.loadData()
    let page = document.getElementById('app')
    page.style.top = '0'
  },
  // beforeRouteLeave(to, from, next) {
  //   if (to.matched[0].path === NORMAL_VISIT_PATH || to.matched[0].path === CHECK_VISIT_PATH) {
  //     // Promise.all([
  //     //   this.queryVisitDetail(),
  //     //   this.queryStoreProductList(),
  //     //   this.queryStoreDetail(this.getSelectedVisitEle.storeId),
  //     //   this.queryStorePerformance(this.getSelectedVisitEle.storeId),
  //     // ]).then(() => {
  //       next()
  //     // })
  //   } else if (to.matched[0].path === ABNORMAL_VISIT_PATH) {
  //     this.queryUnVisitStoreStatus().then(() => {
  //       next()  
  //     })
  //   } else {
  //     next()
  //   }    
  // },
  methods: {
    //  home 
    ...mapActions('home', [
      'setSelectedVisitEle', // vuex  
      'queryDailyVisitPlan', //  home 
      'queryUnVisitStoreStatus', //    
    ]),
    //   
    ...mapActions('normalVisit', [
      'queryStoreProductList', //   
      'queryVisitDetail', //  
    ]),
    //   
    ...mapActions('store', [
      'queryStoreDetail', //    
      'queryStorePerformance', //   
      'queryWechatMarketPlaceWithId', //    
    ]),
    ...mapMutations('store', [
      'SET_SELECTED_LIST_TYPE',
    ]),
    ...mapActions('personal', [
      'queryEmpInfo',
    ]),
    // rightBtnEvent() {
    //   console.log('Im in home')
    // },
    cellHandler(homePageInfo) {
      const hadUnfinished = Array.isArray(this.unfinishedVisits) && this.unfinishedVisits.length > 0 // 1
      if (hadUnfinished) {
        const isUnfinishedItem = homePageInfo.storeId == this.unfinishedVisits[0].storeId
        if (!isUnfinishedItem) {
          this.$vux.alert.show({
            title: '',
            content: ` ${this.unfinishedVisits[0].storeName} `,
          })
          return
        }
      }
      this.SET_SELECTED_LIST_TYPE('store')
      this.setSelectedVisitEle(homePageInfo)
      if (homePageInfo.targetType == 1) { // 0:, 1:
        this.SET_SELECTED_LIST_TYPE('suggested')
        this.queryWechatMarketPlaceWithId({ poiId: homePageInfo.storeId }).then((resp) => {})
      }
      if (homePageInfo.visitStatus == 2 || homePageInfo.visitStatus == 3) { // 0 1 2 3
        this.queryVisitData(homePageInfo.storeId).then(() => {
          this.$router.push('/check_visit/check_info') //  checkVisit
        })
      } else if (homePageInfo.visitStatus == 1) { 
         if (homePageInfo.visitType == 1) { // 1 2 
           this.coverDialogHandler(this.NORMAL)
         } else {
           this.coverDialogHandler(this.ABNORMAL)
         }
      } else {
        this.coverDialogVisible = true
      }
    },
    coverDialogHandler(visitType) {
      this.coverDialogVisible = false
      if (visitType === this.NORMAL) {
        // this.queryVisitData(this.getSelectedVisitEle.storeId).then(() => {
          this.$router.push('normal_visit/general_photo_collection')
        // })
      } else if (visitType === this.ABNORMAL) {
        // this.queryUnVisitStoreStatus(this.getSelectedVisitEle.visitSid).then(() => {
          this.$router.push('abnormal_visit')
        // })
      }
    },
    showCoverDialog(visible, homePageInfo) {
      this.coverDialogVisible = visible
    },
    queryVisitData(storeId) {
      return new Promise((resolve, reject) => {
        Promise.all([
          this.queryVisitDetail(),
          this.queryStoreProductList(),
          this.queryStoreDetail(storeId),
          this.queryStorePerformance(storeId),
        ]).then(() => {
          resolve()
        }).catch(() => {
          reject()
        })
      })
    },
  },
}



